@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

$black_coffee: #252525;

*,
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  font-weight: 300;

  a,
  a:active,
  a:visited {
    text-decoration: none;
    color: inherit;
  }
}

.about,
.contact {
  position: relative;
  z-index: 2;
}

.header {
  height: 100vh;
  background-color: $black_coffee;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: right;
  color: white;

  img {
    margin-top: 10vh;
    height: 90vh;
    position: relative;
  }

  div {
    padding: 8px 10px 8px 0;
    border-right: 2px white solid;
  }

  h1 {
    font-size: 350%;
  }
  .subtitle {
    display: block;
    font-size: 200%;
  }
}

.about,
.contact,
.projects {
  display: block;
  min-height: 300px;
  padding: 32px 20%;

  h2 {
    font-size: 300%;
    margin-bottom: 16px;
  }

  p {
    padding-left: 24px;
    text-align: justify;
  }
}

.projects {
  background-color: $black_coffee;
  color: white;

  div {
    margin-bottom: 16px;

    h4 {
      font-size: 250%;
    }

    h5 {
      font-size: 125%;
    }

    p {
      margin: 16px 0;
    }

    a {
      font-size: 125%;
      display: block;
      width: fit-content;
    }

    .un::after {
      content: "";
      width: 0px;
      height: 1px;
      display: block;
      background: white;
      transition: 300ms;
    }

    .un:hover::after {
      width: 100%;
    }
  }
}

.contact {
  ul {
    list-style: none;
    padding-left: 24px;

    .iconOnHover {
      transition: 0.2s;
    }

    .iconOnHover[type="LinkedIn"]:hover {
      color: #0077b5;
    }
    .iconOnHover[type="FB"]:hover {
      color: #1877f2;
    }
    .iconOnHover[type="Twitter"]:hover {
      color: #55acee;
    }
    .iconOnHover[type="DC"]:hover {
      color: #5865f2;
    }

    span {
      margin-left: 8px;
    }
  }
}

.footer {
  background-color: $black_coffee;
  width: 100%;
  display: block;
  text-align: center;
  padding: 24px;
  color: white;
}

@media (max-width: 776px) {
  .header {
    flex-direction: column-reverse;
    justify-content: space-between;

    div {
      margin-top: 42px;
    }

    img {
      height: 70vh;
    }
  }
  .about,
  .contact,
  .projects {
    padding: 32px 5%;

    h2 {
      font-size: 300%;
      margin-bottom: 16px;
    }

    p {
      padding-left: 8px;
      text-align: justify;
    }
  }

  .contact {
    ul {
      padding-left: 8px;
    }
  }
}
